.registerPage {
    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center; 
    height: 100vh; 
    padding: 20px;
  }
  
  .form {
    display: flex;
    flex-direction: column; 
    align-items: center;
    gap: 15px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    width: 300px;
  }
  
  .formHeader {
    margin-bottom: 20px;
    font-size: 24px;
    color: rgb(27, 52, 85);
    text-align: center; 
  }
  
  .inputGroup {
    width: 100%;
  }
  
  .label {
    margin-bottom: 8px;
    font-size: 16px;
    color: rgb(27, 52, 85);
  }
  
  .input {
    width: 280px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    margin-top: 8px;
  }
  
  .button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #495796;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #45a049;
  }
  